@use 'base/variables' as v;
@use 'base/mixins' as m;

//ESTILOS DE LA PAGINA PROYECTOS

.no-proyectos{

    a{
        color: v.$indigo;
    }
}

.listado-proyectos{
    list-style: none;
    padding: 0;
    margin-top: 3rem;

    @include m.grid(1, 2rem);//1 columna
    @include m.tablet {
        @include m.grid(2, 2rem);//2 columnas
    }
    @include m.desktop {
        @include m.grid(3, 2rem);//3 columnas
    }

    .proyecto{
        background-color: lighten(v.$indigo, 5%);
        min-height: 12rem;
        display: flex;
        align-items: center;
        padding: 2rem;
        line-height: 2;
        border-radius: .5rem;
        cursor: pointer;

        &:hover{
            background-color: darken(v.$indigo, 5%);
        }

        a{
            color: v.$blanco;
        }
    }
}