@use 'base/variables' as v;
@use 'base/mixins' as m;

//ESTILOS PARA EL TITULO UPDTASK, Y LA PAGINA DE LOGIN

.login{
    margin-top: 5rem;

    .uptask{
        @include m.textoDegradado(v.$cyan);
    }

    input[type="submit"]{
        @include m.boton(v.$cyan, v.$blanco)
    }

    .acciones{
        a{
            color: v.$cyan;
        }
    }
}