@use 'base/variables' as v;

/** Media Queries **/
@mixin telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}
@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}
@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}


@mixin boton($bgColor, $colorTexto) {
    background-color: $bgColor;
    padding: 1.5rem 4rem;
    color: $colorTexto;
    font-size: 1.6rem;
    display: inline-block;
    font-weight: 700;
    border: none;
    transition-property: background-color;
    transition-duration: .3s;
    text-align: center;
    display: block;
    width: 100%;
    @include tablet {
        width: auto;
    }
    &:hover {
        background-color: darken( $bgColor, 10% );
        cursor: pointer;
    }
}

@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}

@mixin textoDegradado($color){
    background: linear-gradient(to right, $color 0%, lighten($color, 30%) 100%); //degradado
        color: transparent; //desaparece el text
        background-clip: text; //agrega el degradado al texto
        font-size: 7rem;
        text-align: center;

        @include tablet {
            font-size: 10rem;
        }
}

@mixin scroll($color1, $color2){
    /* ESTILOS DEL SCROLL */
    &::-webkit-scrollbar{
        width: 1rem;     /* Tamaño del scroll en vertical */
        height: 1rem;    /* Tamaño del scroll en horizontal */
    }
    &::-webkit-scrollbar-thumb {
        background: $color1;
        border-radius: 4px;
    }
    &::-webkit-scrollbar-thumb:hover {
        background: $color2;
        box-shadow: $color2;
    }
}