@use 'base/variables' as v;
@use 'base/mixins' as m;

//ESTILOS DE LOS ENLACES A CREAR CUENTA, OLVIDE PASSWORD, LOGIN

.acciones{
    margin-top: 5rem;

    @include m.tablet {
        display: flex;
        justify-content: space-between;
    }

    a{
        display: block;
        text-align: center;
        margin-bottom: 1rem;
        color: v.$negro;
        &:last-of-type{
            margin-bottom: 0;
        }
    }
}