@use 'base/variables' as v;
@use 'base/mixins' as m;

//ESTILOS EN LA BARRA LATERAL DE DASHBOARD

.sidebar{
    opacity: 0;
    background-color: v.$indigo;
    height: 0;
    overflow: hidden;
    transition-property: opacity;
    transition-duration: 500ms;

    //ESTILOS PARA MOSTRAR EL MENU DESPLEGABLE EN CELULARES
    &.mostrar{
        padding: 2rem;
        opacity: 1;
        height: 100%;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background-color: rgb(0 0 0 / .90);
        @include m.tablet {
            display: none;
        }
    }
    &.ocultar{
        opacity: 0;
    }
    @include m.tablet {
        opacity: 1;
        height: 100%;
        padding: 2rem;
        background-color: v.$indigo;
    }

    .contenedor-sidebar{
        display: flex;
        justify-content: space-between;
        align-items: center;
        h2{
            color: v.$blanco;
            margin: 0;
            text-align: center;
        }

        .cerrar-menu{
            display: flex;
            align-items: center;
            height: 2rem;
            width: 2rem;
            img{
                &:hover{
                    cursor: pointer;
                }
            }
            @include m.tablet {
                display: none;
            }
        }
    }

    .sidebar-nav{
        margin-top: 5rem;

        a{
            color: v.$blanco;
            display: block;
            margin-bottom: 2.5rem;
            padding: .7rem;
            text-transform: uppercase;
            font-weight: bold;
            font-size: 2.4rem;
            transition-property: background-color;
            transition-duration: 300ms;
            text-align: center;

            &:hover,
            &.activo{
                background-color: darken(v.$indigo, 20%);
                border-radius: .5rem;
            }

            @include m.tablet {
                font-size: 1.5rem;
                margin-bottom: 1.5rem;
                text-align: left;
            }
        }
    }

    .cerrar-sesion-mobile{
        margin-top: 10rem;
        .cerrar-sesion{
            @include m.boton(v.$indigo, v.$blanco);
        }

        @include m.tablet {
            display: none;
        }
    }
}