@use 'base/variables' as v;
@use 'base/mixins' as m;

//ESTILOS EN LA PAGINA DEL MODAL

.modal{
    position: absolute;
    background-color: rgb(0 0 0 / .8);
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    .formulario{
        max-width: 60rem;
        background-color: v.$blanco;
        padding: 2rem;
        margin: 0px auto;
        border-radius: 2rem;
        transform: translateY(-30%);
        transition-property: all;
        transition-duration: 800ms;
        opacity: 0;

        input[type="text"]{
            border: 1px solid v.$gris;
            border-radius: .5rem;
        }
        
        .opciones{
            display: flex;
            justify-content: space-between;
        }

        .submit-nueva-tarea{
            @include m.boton(v.$indigo, v.$blanco);
        }

        .cerrar-modal{
            @include m.boton(v.$naranja, v.$blanco);
        }

        &.animar{
            position: relative;
            top: 50%;
            transform: translateY(-70%);//50% del tamaño del modal y lo sube ese 50%
            opacity: 1;
        }

        &.cerrar{
            transform: translateY(50%);
            opacity: 0;
        }

        legend{
            font-size: 2rem;
            text-align: center;
            font-weight: bold;
            margin-bottom: 2rem;
            display: block;
        }

        
    }
}
